/* Body Styles */

body {
	font-family: sans-serif;
	background-color: #FEF2D8;
	line-height: 1.4em;
	padding-left: 5%;
	padding-right: 5%;
	padding-top: 1em;
	font-size: 16px;
}

h2 {
	line-height: 1.2em;
}

a {
	padding: 1px 1px 0;
	text-decoration: none;
}

a:link {
	color: #B0131D;
}

a:visited {
	color: #B0131D;
}

a:focus {
	border-bottom: 2px solid #B0131D;
	background-color: #FFD3CD;
}

a:hover {
	border-bottom: 2px solid #B0131D;
	background-color: #FFD3CD;
}

.active {
	border-bottom: 2px solid;
}

/* Navbar Styles */

nav {
	padding-bottom: 1em;
	font-size: 1.1em;
}

nav a {
	margin-right: 0.5em;
}

/* Footer Styles */
footer {
	text-align: center;
	padding-top: 2em;
}

/* Main Block Styles */

main {
	background-color: white;
	border: 3px solid #F0535D;
	border-radius: 2px;
	box-shadow: 7px 7px #E0D0C0;
	max-width: 45em;
	margin: 2em auto 2em auto;
	padding: 0em 2em 2em 2em;
	overflow: auto; /*to accomodate floating divs*/
}

main hr {
	border: 2px solid #F0535D;
}

main h1 {
	text-align: center;
}

main a {
	padding: 1px 1px 0;
	text-decoration: none;
}

main a:link {
	color: #0060CC;
	border-bottom: 2px solid;
}

main a:visited {
	color: #064A8C;
}

main a:focus {
	border-bottom: 2px solid #064A8C;
	background-color: #99E9FF;
	color: #064A8C;
}

main a:hover {
	border-bottom: 2px solid #064A8C;
	background-color: #99E9FF;
	color: #064A8C;
}

/* About Styles */

.about-img {
	float: left;
	width: 30%;
	padding-top: 1em;
}

.about-text {
	float: left;
	width: 65%;
	margin-left: 5%;
}

/* Project Styles */

.devicon-span {
	float: right;
}

.project {
	padding-left: 3em;
	padding-bottom: 1em;
}

/* Code Highlighting */

.highlight pre {
	overflow: auto;
	font-size: 14px;
}

.code-inline {
	font-family: monospace;
	color: #B0131D;
	font-size: 14px;
}

/* Mobile Styles */

@media only screen and (max-width: 600px) {
	.about-img {
		width: 50%;
		float: none;
		display: block;
		margin: auto;
		padding-top: 0;
	}
	
	.about-text {
		width: 100%;
		float: none;
		margin: auto;
	}
}